import styled from "styled-components"
import Announcement from "../components/Announcement"
import Newsletter from "../components/Newsletter"
import Navbar from "../components/Navbar"
import Footer from "../components/Footer"
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';
import RemoveOutlinedIcon from '@mui/icons-material/RemoveOutlined';
import { mobile } from "../responsive"
import { useLocation } from "react-router-dom"
import { useEffect, useState } from "react"
// import axios from "axios"
import { publicRequest } from "../requestMethods"
import { addProduct } from "../redux/cartRedux"
import { useDispatch } from "react-redux"


const Container= styled.div`
    
`
const Wrapper= styled.div`
    padding: 50px;
    display: flex;
    ${mobile({ padding: "10px", flexDirection:"column"})}
`
const ImgContainer= styled.div`
    flex:1;
`
const Image= styled.img`
    width: 100%;
    height: 90vh;
    object-fit: cover;
    ${mobile({ height:"50vh" , objectFit: "contain"})}

`
const InfoContainer= styled.div`
    flex:1;
    padding: 0px 50px;
    ${mobile({ padding: "10px"})}
`
const Title= styled.h1`
   font-weight: 200; 
`
const Desc= styled.p`
    margin: 20px 0px;
`
const Price= styled.span`
    font-weight: 100;
    font-size: 40px;
`
const FilterContainer = styled.div`
    width: 50%;
    margin: 30px 0px;
    display: flex;
    justify-content: space-between;
    ${mobile({ width: "100%"})}

`
const Filter = styled.div`
    display: flex;
    align-items: center;
`
const FilterTitle = styled.span`
    font-size: 20px;
    font-weight: 200;
`

const FilterColor = styled.div`
  width: 20px;
  height: 20px;
  border: 1px solid black;
  border-radius: 50%;
  background-color: ${props=>props.color};
  margin: 0px 5px;
  cursor: pointer;
  
`

const FilterTon= styled.select`
    margin: 10px;
    padding: 5px;
`
const FilterTonOption = styled.option``
const FilterStar= styled.select`
    margin: 10px;
    padding: 5px;
`
const FilterStarOption= styled.option``
const AddContainer= styled.div`
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    ${mobile({ width: "100%"})}

`
const AmountContainer= styled.div`
    display: flex;
    align-items: center;
    font-weight: 700;
`
const Amount= styled.span`
    width: 30px;
    height: 30px;
    border-radius: 10px;
    border: 1px solid teal;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px 5px;
`
const Button= styled.button`
    padding: 15px;
    border: 2px solid teal;
    background-color: white;
    cursor: pointer;
    font-weight: 500;
    ${mobile({ padding: "8px"})}

    &:hover{
        background-color: #f8f4f4;
    }
`


const Product = () => {
    const location= useLocation();
    const id= location.pathname.split("/")[2];
    const [product, setProduct] = useState({});
    const [quantity, setQuantity] = useState(1);
    const [ton, setTon] = useState("");
    const [star, setStar] = useState("");
    const [color, setColor] = useState("");
    const dispatch = useDispatch()


    useEffect(()=> {
        const getProduct = async ()=> {
            try{
                const res = await publicRequest.get("/products/find/"+id)
                setProduct(res.data);
            }catch{}
        };
        getProduct()
    }, [id])

    const handleQuantity = (type) => {
        if(type === "dec"){
           quantity > 1 && setQuantity(quantity - 1)
        } else{
            setQuantity(quantity + 1)
        }
    }

 const handleClick = ()=>{
    dispatch(addProduct({...product, quantity, color, ton, star}))
    // axios.post()
 }

  return (
    <Container>
      <Announcement />
      <Navbar/>
      <Wrapper>
        <ImgContainer>
            <Image src= {product.img}/>
        </ImgContainer>
        <InfoContainer>
            <Title>{product.title}</Title>
            <Desc>
            {product.desc}
            </Desc>
            <Price> {product.price}</Price>
            <FilterContainer>
  <Filter>
    <FilterTitle>Color </FilterTitle>
    {product.color &&
      product.color?.map((c) => (
        <FilterColor color={c} key={c} onClick={()=> setColor(c)}/>
      ))}
  </Filter>
  <Filter>
    <FilterTitle>Ton: </FilterTitle>
    <FilterTon onChange={(e)=>setTon(e.target.value)}>
        {Array.isArray(product.ton) &&
    product.ton.map((t) => <FilterTonOption key={t}>{t}</FilterTonOption>)}
    </FilterTon>
  </Filter>
  <Filter>
    <FilterTitle>Star: </FilterTitle>
    <FilterStar onChange={(e)=>setStar(e.target.value)}>
  {Array.isArray(product.star) &&
    product.star.map((s) => <FilterStarOption key={s}>{s}</FilterStarOption>)}
</FilterStar>

  </Filter>
</FilterContainer>

            <AddContainer>
                <AmountContainer>
                    <RemoveOutlinedIcon onClick={()=> handleQuantity("dec")}/>
                    <Amount>{quantity}</Amount>
                    <AddOutlinedIcon onClick={()=> handleQuantity("inc")} />
                </AmountContainer>
                <Button onClick={handleClick}>ADD TO CART</Button>
            </AddContainer>
        </InfoContainer>
      </Wrapper>
      <Newsletter />
      <Footer/>
    </Container>
  )
}

export default Product
