import styled from "styled-components"
// import { popularProducts } from "../data";
import Product from "./Product";
import { useEffect, useState } from "react";
import axios from "axios";

const Container = styled.div`
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
`

const Products = ({cat, filters, sort}) => {
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);

  useEffect(()=>{
    const getProducts = async ()=>{
      try{
        const res = await axios.get(cat ? `https://ecommerce-mern-cwyq.onrender.com/api/products?category=${cat}` : "https://ecommerce-mern-cwyq.onrender.com/api/products");
        setProducts(res.data);
      }catch(err){}
    };
    getProducts()
  },[cat]);

  useEffect(() => {
    if (filters && Object.keys(filters).length === 0) {
      setFilteredProducts(products);
    } else {
      setFilteredProducts(
        products.filter(item =>
          filters && Object.entries(filters).every(([key, value]) =>
            item[key] && item[key].includes(value)
          )
        )
      );
    }
  }, [products, cat, filters]);
  

  useEffect(() => {
    if (products.length > 0) {
      if (sort === "newest") {
        setFilteredProducts(prev =>
          [...prev].sort((a, b) => a.createdAt - b.createdAt)
        );
      } else if (sort === "asc") {
        setFilteredProducts(prev =>
          [...prev].sort((a, b) => a.price - b.price)
        );
      } else {
        setFilteredProducts(prev =>
          [...prev].sort((a, b) => b.price - a.price)
        );
      }
    }
  }, [products, sort]);
  
  
  
  return (
    <Container>
      {cat ? filteredProducts.map((item)=>(
        <Product item={item} key={item.id}/>
      )) : products
        .slice(0,8)
        .map((item)=> <Product item={item} key={item.id}/>
      )}
    </Container>
  )
}

export default Products
